<template>
  <div class="my-container">
    <app-header></app-header>

    <section id="map"></section>
    <aside id="sidebar" class="sidebar collapsed">
      <!-- Nav tabs -->
      <div class="sidebar-tabs">
        <ul role="tablist"> <!-- top aligned tabs -->
          <li><a href="#drawing-tools" role="tab"><i class="fa fa-bars"></i></a></li>
        </ul>
      </div>

      <!-- Tab panes -->
      <div class="sidebar-content">
        <div class="sidebar-pane" id="drawing-tools">
          <h1 class="sidebar-header">Draw modes
          </h1>
          <section>
            <button class="button"
                    :class="{active: activeTag === 'IssueA'}"
                    @click="onBtnIssueAClicked($event)"
                    id="btnIssueAClicked">
              Issue A
            </button>
            <button class="button"
                    :class="{active: activeTag === 'IssueB'}"
                    @click="onBtnIssueBClicked($event)"
                    id="btnIssueBClicked">
              Issue B
            </button>
            <button class="button"
                    :class="{active: activeTag === 'IssueC'}"
                    @click="onBtnIssueCClicked($event)"
                    id="btnIssueCClicked">
              Issue C
            </button>
            <button class="button"
                    :class="{active: activeTag === 'IssueD'}"
                    @click="onBtnIssueDClicked($event)"
                    id="btnIssueDClicked">
              Issue D
            </button>
            <button class="button"
                    :class="{active: activeTag === 'IssueE'}"
                    @click="onBtnIssueEClicked($event)"
                    id="btnIssueEClicked">
              Issue E
            </button>
            <button class="button"
                    :class="{active: activeTag === 'IssueF'}"
                    @click="onBtnIssueFClicked($event)"
                    id="btnIssueFClicked">
              Issue F
            </button>
            <button class="button"
                    :class="{active: activeTag === 'IssueG'}"
                    @click="onBtnIssueGClicked($event)"
                    id="btnIssueGClicked">
              Issue G
            </button>
            <button class="button"
                    :class="{active: activeTag === 'IssueH'}"
                    @click="onBtnIssueHClicked($event)"
                    id="btnIssueHClicked">
              Issue H
            </button>
          </section>
        </div>
      </div>
    </aside>

    <app-footer></app-footer>
  </div>
</template>

<script>
  import Header from './Header.vue';
  import Footer from './Footer.vue';
  import L from 'leaflet';
  import 'leaflet-draw';
  import 'leaflet-draw-drag';
  import 'leaflet-sidebar-v2';
  import 'leaflet-easybutton'
  import './scripts/leaflet-tag-filter-button'
  import './scripts/Leaflet.Control.Custom';
  import {copyShape} from './scripts/copyShape';

  export default {
    components: {
      'app-header': Header,
      'app-footer': Footer,
    },
    data() {
      return {
        map: null,
        featureGroupAllItems: null,  // this featureGroup stores ALL the drawn vectors
        selectedItems: {},
        activeTag: '',
        isEditing: false,
      };
    },
    mounted() {
      let self = this;
      let map = L.map('map', {attributionControl: false}).setView([22.9734, 78.6569], 5);
      this.map = map;
      L.tileLayer('http://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {maxZoom: 18,}).addTo(map);
      let allItems = new L.FeatureGroup();
      this.featureGroupAllItems = allItems;
      map.addLayer(allItems);

      let drawControl = new L.Control.Draw({
        draw: {
          polyline: false,
          marker: false,
          circlemarker: false,
          polygon: {
            shapeOptions: {
              color: '#999',
            },
            allowIntersection: false,
            drawError: {
              color: '#ff6244',
              timeout: 1000
            },
            showArea: true,
            repeatMode: true,
          },
          rectangle: {
            shapeOptions: {
              color: '#999',
            },
            showArea: true,
            repeatMode: true,
          },
          circle: {
            shapeOptions: {
              color: '#999',
            },
            showRadius: true,
            repeatMode: true,
          }
        },
        edit: {
          featureGroup: allItems,
          edit: {
            moveMarkers: false,
            selectedPathOptions: {
              maintainColor: true,
            },
          },
        }
      });
      map.addControl(drawControl);

      map.on(L.Draw.Event.CREATED, function (e) {
        let type = e.layerType,
          layer = e.layer;

        layer.on('click', e => {
          self.shapeClicked(e);
        });
        layer.on('drag', e => {
          self.shapeDrag(e);
        });
        layer.on('dragstart', e => {
          self.shapeDragStart(e);
        });
        layer.on('dragend', e => {
          self.shapeDragEnd(e);
        });

        layer.internals = {};
        layer.internals['category'] = '';
        layer.internals['description'] = '';
        layer.internals['type'] = type;
        layer.internals['selected'] = false;

        if (self.activeTag !== '') {
          layer.options.tags = [self.activeTag];
        } else {
          layer.options.tags = [];
        }
        self.applyStyle(layer);

        allItems.addLayer(layer);
      });

      map.on(L.Draw.Event.EDITSTART, function (e) {
        self.isEditing = true;
        self.featureGroupAllItems.eachLayer(function (layer) {
          self.applyStyle(layer);
        });
      });

      map.on(L.Draw.Event.EDITSTOP, function (e) {
        self.isEditing = false;
        self.featureGroupAllItems.eachLayer(function (layer) {
          self.applyStyle(layer);
        });
      });

      L.control.sidebar({
        autopan: true,   // whether to maintain the centered map point when opening the sidebar
        closeButton: true,// whether to add a close button to the panes
        id: 'sidebar',    // the DOM ID of a predefined sidebar container which should be used
        position: 'right', // left or right
      }).addTo(map);

      let btnForward = L.easyButton({
        id: 'id-for-the-button',  // an id for the generated button
        position: 'topleft',      // inherited from L.Control -- the corner it goes in
//        type: 'replace',          // set to animate when you're comfy with css
        leafletClasses: true,     // use leaflet classes to style the button?
        states: [{                 // specify different icons and responses for your button
//          stateName: 'get-center',
          onClick: () => this.layerBringToFront(),
          title: 'Bring to front',
          icon: 'fa-level-up',
        }]
      });
      let btnBack = L.easyButton({
        id: 'id-for-the-button',  // an id for the generated button
        position: 'topleft',      // inherited from L.Control -- the corner it goes in
//        type: 'replace',          // set to animate when you're comfy with css
        leafletClasses: true,     // use leaflet classes to style the button?
        states: [{                 // specify different icons and responses for your button
//          stateName: 'get-center',
          onClick: () => this.layerSendToBack(),
          title: 'Send to back',
          icon: 'fa-level-down',
        }]
      });
      let buttons = [btnForward, btnBack];
      L.easyBar(buttons).addTo(map);

      L.control.tagFilterButton({
        data: [
          'IssueA',
          'IssueB',
          'IssueC',
          'IssueD',
          'IssueE',
          'IssueF',
          'IssueG',
          'IssueH',
        ]
      }).addTo(map);

    },
    methods: {
      removeFromArray(array, element) {
        return array.filter(e => e !== element);
      },
      shapeClicked(data) {
        let layer = data.target;
        if (layer.internals.selected) {
          delete this.selectedItems[L.Util.stamp(layer)];
        } else {
          this.selectedItems[L.Util.stamp(layer)] = layer;
        }
        layer.internals.selected = !layer.internals.selected;
        this.applyStyle(layer);
      },
      shapeDragStart(data) {
        if (data.ctrlKey) {
          for (let item of Object.values(this.selectedItems)) {
            let clone = copyShape(item);
            clone.on('click', e => {
              this.shapeClicked(e);
            });
            clone.on('drag', e => {
              this.shapeDrag(e);
            });
            clone.on('dragstart', e => {
              this.shapeDragStart(e);
            });
            clone.on('dragend', e => {
              this.shapeDragEnd(e);
            });
            this.featureGroupAllItems.addLayer(clone);
          }
        }
      },
      shapeDrag(data) {
        let target = data.target;
        for (let key of Object.keys(this.selectedItems)) {
          let otherLayer = this.selectedItems[key];
          if (otherLayer !== target && otherLayer._transform) {
//            let td = target.dragging;
//            console.log(td);
//            let tdm = target.dragging._matrix;
//            console.log(tdm);
            otherLayer._transform(target.dragging._matrix);
          }
        }
      },
      shapeDragEnd(data) {
        let target = data.target;
        for (let key of Object.keys(this.selectedItems)) {
          let otherLayer = this.selectedItems[key];
          if (otherLayer !== target && otherLayer.dragging && otherLayer.dragging._transformPoints) {
            otherLayer.dragging._transformPoints(target.dragging._matrix);
            otherLayer._updatePath();
            otherLayer._project();
            otherLayer._transform(null);
          }
        }
      },
      applyStyle(layer) {
        let highlightStyle = {
          weight: 5,
          fill: true,
          fillOpacity: 0.6,
          dashArray: '15, 15'
        }, editingStyle = {
          weight: 4,
          opacity: 0.9,
          fill: true,
          fillOpacity: 0.1,
          dashArray: '10, 10'
        }, defaultStyle = {
          color: '#999',
          weight: 4,
          opacity: 0.9,
          fill: true,
          fillColor: '#999',
          fillOpacity: 0.2,
          dashArray: '1'
        };
        let tagStyles = {
          IssueA: {
            color: '#cf4fc6',
            weight: 4,
            opacity: 0.9,
            fillOpacity: 0.2,
            dashArray: '1',
          },
          IssueB: {
            color: '#579147',
            weight: 4,
            opacity: 0.9,
            fillOpacity: 0.2,
            dashArray: '1',
          },
          IssueC: {
            color: '#7547c8',
            weight: 4,
            opacity: 0.9,
            fillOpacity: 0.2,
            dashArray: '1',
          },
          IssueD: {
            color: '#ac7f34',
            weight: 4,
            opacity: 0.9,
            fillOpacity: 0.2,
            dashArray: '1',
          },
          IssueE: {
            color: '#6575be',
            weight: 4,
            opacity: 0.9,
            fillOpacity: 0.2,
            dashArray: '1',
          },
          IssueF: {
            color: '#db4d3a',
            weight: 4,
            opacity: 0.9,
            fillOpacity: 0.2,
            dashArray: '1',
          },
          IssueG: {
            color: '#b44b82',
            weight: 4,
            opacity: 0.9,
            fillOpacity: 0.2,
            dashArray: '1',
          },
          IssueH: {
            color: '#9f4b41',
            weight: 4,
            opacity: 0.9,
            fillOpacity: 0.2,
            dashArray: '1',
          },
        };
        if ((layer.options.tags.length > 0)) {
          layer.setStyle(tagStyles[layer.options.tags[0]]);
        } else {
          layer.setStyle(defaultStyle);
        }
        if (this.isEditing) {
          layer.setStyle(editingStyle);
        }
        if (layer.internals['selected']) {
          layer.setStyle(highlightStyle);
        }
        console.log(layer.options);
      },
      layerSendToBack() {
        for (let item of Object.values(this.selectedItems)) {
          item.bringToBack();
        }
      },
      layerBringToFront() {
        for (let item of Object.values(this.selectedItems)) {
          item.bringToFront();
        }
      },
      onBtnIssueAClicked(e) {
        for (let item of Object.values(this.selectedItems)) {
          if ((item.options.tags.length > 0) && (item.options.tags[0] === 'IssueA')) {
            item.options.tags = []
          } else {
            item.options.tags = ['IssueA'];
          }
          this.applyStyle(item);
        }
        if (!this.selectedItems.length) {
          if (this.activeTag === 'IssueA')
            this.activeTag = '';
          else
            this.activeTag = 'IssueA';
        }
      },
      onBtnIssueBClicked(e) {
        for (let item of Object.values(this.selectedItems)) {
          if ((item.options.tags.length > 0) && (item.options.tags[0] === 'IssueB')) {
            item.options.tags = []
          } else {
            item.options.tags = ['IssueB'];
          }
          this.applyStyle(item);
        }
        if (!this.selectedItems.length) {
          if (this.activeTag === 'IssueB')
            this.activeTag = '';
          else
            this.activeTag = 'IssueB';
        }
      },
      onBtnIssueCClicked(e) {
        for (let item of Object.values(this.selectedItems)) {
          if ((item.options.tags.length > 0) && (item.options.tags[0] === 'IssueC')) {
            item.options.tags = []
          } else {
            item.options.tags = ['IssueC'];
          }
          this.applyStyle(item);
        }
        if (!this.selectedItems.length) {
          if (this.activeTag === 'IssueC')
            this.activeTag = '';
          else
            this.activeTag = 'IssueC';
        }
      },
      onBtnIssueDClicked(e) {
        for (let item of Object.values(this.selectedItems)) {
          if ((item.options.tags.length > 0) && (item.options.tags[0] === 'IssueD')) {
            item.options.tags = []
          } else {
            item.options.tags = ['IssueD'];
          }
          this.applyStyle(item);
        }
        if (!this.selectedItems.length) {
          if (this.activeTag === 'IssueD')
            this.activeTag = '';
          else
            this.activeTag = 'IssueD';
        }
      },
      onBtnIssueEClicked(e) {
        for (let item of Object.values(this.selectedItems)) {
          if ((item.options.tags.length > 0) && (item.options.tags[0] === 'IssueE')) {
            item.options.tags = []
          } else {
            item.options.tags = ['IssueE'];
          }
          this.applyStyle(item);
        }
        if (!this.selectedItems.length) {
          if (this.activeTag === 'IssueE')
            this.activeTag = '';
          else
            this.activeTag = 'IssueE';
        }
      },
      onBtnIssueFClicked(e) {
        for (let item of Object.values(this.selectedItems)) {
          if ((item.options.tags.length > 0) && (item.options.tags[0] === 'IssueF')) {
            item.options.tags = []
          } else {
            item.options.tags = ['IssueF'];
          }
          this.applyStyle(item);
        }
        if (!this.selectedItems.length) {
          if (this.activeTag === 'IssueF')
            this.activeTag = '';
          else
            this.activeTag = 'IssueF';
        }
      },
      onBtnIssueGClicked(e) {
        for (let item of Object.values(this.selectedItems)) {
          if ((item.options.tags.length > 0) && (item.options.tags[0] === 'IssueG')) {
            item.options.tags = []
          } else {
            item.options.tags = ['IssueG'];
          }
          this.applyStyle(item);
        }
        if (!this.selectedItems.length) {
          if (this.activeTag === 'IssueG')
            this.activeTag = '';
          else
            this.activeTag = 'IssueG';
        }
      },
      onBtnIssueHClicked(e) {
        for (let item of Object.values(this.selectedItems)) {
          if ((item.options.tags.length > 0) && (item.options.tags[0] === 'IssueH')) {
            item.options.tags = []
          } else {
            item.options.tags = ['IssueH'];
          }
          this.applyStyle(item);
        }
        if (!this.selectedItems.length) {
          if (this.activeTag === 'IssueH')
            this.activeTag = '';
          else
            this.activeTag = 'IssueH';
        }
      },
    }
  }
</script>

<style lang="scss" scoped>
  @import "../node_modules/leaflet/dist/leaflet.css";
  @import "../node_modules/leaflet-draw/dist/leaflet.draw.css";
  @import "../node_modules/leaflet-easybutton/src/easy-button.css";
  @import "stylesheets/leaflet-tag-filter-button.css";
  @import "stylesheets/sidebar.css";

  $colIssueA: '#cf4fc6';
  $colIssueB: '#579147';
  $colIssueC: '#7547c8';
  $colIssueD: '#ac7f34';
  $colIssueE: '#6575be';
  $colIssueF: '#db4d3a';
  $colIssueG: '#b44b82';
  $colIssueH: '#9f4b41';

  aside {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: .3em;
    box-shadow: -10px 1px 30px -15px rgba(0, 0, 0, 1);
    z-index: 1000;
  }

  section {
    display: flex;
    flex-direction: column;
  }

  .sidebar-pane > section {
    padding-top: 1em;

    .button {
      margin-bottom: .7em;
    }
  }

  .my-container {
    display: flex;
    flex-direction: column;
    width: 100vw;
    height: 100vh;
    justify-content: center;
    overflow: hidden;
  }

  .leaflet-control-attribution {
    display: none !important;
  }

  #map {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
  }

  #btnIssueAClicked {
    border-color: #cf4fc6;
  }

  #btnIssueAClicked:hover {
    background-color: #cf4fc6;
  }

  #btnIssueAClicked.active {
    background-color: #cf4fc6;
  }

  #btnIssueAClicked.active:hover {
    background-color: #f0f0f0;
  }

  #btnIssueBClicked {
    border-color: #579147;
  }

  #btnIssueBClicked:hover {
    background-color: #579147;
  }

  #btnIssueBClicked.active {
    background-color: #579147;
  }

  #btnIssueBClicked.active:hover {
    background-color: #f0f0f0;
  }

  #btnIssueCClicked {
    border-color: #7547c8;
  }

  #btnIssueCClicked:hover {
    background-color: #7547c8;
  }

  #btnIssueCClicked.active {
    background-color: #7547c8;
  }

  #btnIssueCClicked.active:hover {
    background-color: #f0f0f0;
  }

  #btnIssueDClicked {
    border-color: #ac7f34;
  }

  #btnIssueDClicked:hover {
    background-color: #ac7f34;
  }

  #btnIssueDClicked.active {
    background-color: #ac7f34;
  }

  #btnIssueDClicked.active:hover {
    background-color: #f0f0f0;
  }

  #btnIssueEClicked {
    border-color: #6575be;
  }

  #btnIssueEClicked:hover {
    background-color: #6575be;
  }

  #btnIssueEClicked.active {
    background-color: #6575be;
  }

  #btnIssueEClicked.active:hover {
    background-color: #f0f0f0;
  }

  #btnIssueFClicked {
    border-color: #db4d3a;
  }

  #btnIssueFClicked:hover {
    background-color: #db4d3a;
  }

  #btnIssueFClicked.active {
    background-color: #db4d3a;
  }

  #btnIssueFClicked.active:hover {
    background-color: #f0f0f0;
  }

  #btnIssueGClicked {
    border-color: #b44b82;
  }

  #btnIssueGClicked:hover {
    background-color: #b44b82;
  }

  #btnIssueGClicked.active {
    background-color: #b44b82;
  }

  #btnIssueGClicked.active:hover {
    background-color: #f0f0f0;
  }

  #btnIssueHClicked {
    border-color: #9f4b41;
  }

  #btnIssueHClicked:hover {
    background-color: #9f4b41;
  }

  #btnIssueHClicked.active {
    background-color: #9f4b41;
  }

  #btnIssueHClicked.active:hover {
    background-color: #f0f0f0;
  }
</style>



